@import url(https://fonts.googleapis.com/css2?family=PT+Serif:ital@1&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Serif', serif;
}
.btn--medium, .btn--large, .btn--mobile {
    background-color: transparent;
    color: #e4e4e4;
    border: 1px solid #e4e4e4;
    font-size: 19px;
    border-radius: 5px;
    padding: 8px 20px;
    transition: all 0.5s ease-in-out;
    cursor: pointer;
    outline: none;
    letter-spacing: 1px;
}
.btn--large {
    background-color: #f9004d;
    border: 1px solid #f9004d;
    padding: 12px 40px;
}
.btn--large:hover {
    background-color: transparent;
    border: 1px solid #e4e4e4;
}
.btn--mobile {
    color: #000;
    border: 1px solid #000;
    font-weight: 700;
}
.btn--medium:hover, .btn--mobile:hover {
    background-color: #f9004d;
    border: 1px solid #f9004d;
    color: #fff;
}

.capitalize {
    text-transform: capitalize;
}
.upper {
    text-transform: uppercase;
}
.navbar {
    background-color: rgba(0,0,0,0.9);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 19px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    height: 80px;
    z-index: 999;
}
.navbar-container {
    display: flex;
    justify-content: space-between;
    height: 80px;
}
.container {
    z-index: 1;
    width: 100%;
    max-width: 1200px;
    margin-right: auto;
    margin-left: auto;
    padding-left: 50px;
    padding-right: 50px;
}

.navbar-logo {
    text-decoration: none;
    justify-self: start;
    cursor: pointer;
    width: 110px;
    display: flex;
    align-items: center;
}
.navbar-logo img {
    max-width: 100%;
}
.nav-menu {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    text-align: center;
}
.nav-links {
    text-decoration: none;
    color: #e4e4e4;
    padding: 20px;
    height: 100%;
    transition: all 0.5s ease-in-out;
    text-transform: capitalize;
    letter-spacing: 1px;
}

.nav-links:hover {
    color: #f9004d;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 960px) {
    .nav-menu {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        height: 100vh;
        flex-direction: column;
        position: absolute;
        top: 80px;
        left: -100%;
        transition: all 0.5s ease-in-out;
        opacity: 1;
        padding: 40px 0 0 0;
    }
    .nav-menu.active {
        background-color: #fff;
        left: 0;
        opacity: 1;
        transition: all 0.4s ease-in-out;
        z-index: 1;
    }
    .nav-item {
        margin: 20px 0;
        
    }
    .nav-links {
        color: #000;
        font-weight: 700;
        
    }
    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        font-size: 23px;
        transform: translate(-100%, 100%);
        cursor: pointer;
    }
}
.landing-page {
    position: relative;
    width: 100%;
    height: calc(100vh - 80px );
}
.landing {
    position: absolute;
    top: -80px;
    left: 0;
    width: 100%;
    height: 100vh;
}
.landing img {
  
    width: 100%;
    height: 100vh;
    object-fit: cover;
   
}
.landing-over {
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    text-align: center;
    z-index: 1;
   
    
}
.landing-over h1 {
    color: #fff;
    text-transform: uppercase;
    font-size: 90px;
    font-weight: 900;
    background: linear-gradient(145deg,#f81f01,#ee076e);
    -webkit-background-clip : text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    display: inline-block;
    margin-bottom: 20px;
    letter-spacing: 3px;
}
.landing-over p {
    color: #e4e4e4;
    text-transform: capitalize;
    font-size: 22px;
    line-height: 2;
    margin: 25px 0;
    font-weight: 300;
}

#info {
    height: 1000vh;
    width: 100%;
}







@media screen and (max-width: 771px) {
    .landing-over h1 {
        font-size: 75px;
    }
    .landing-over p {
        font-size: 20px;
    }

}




@media screen and (max-width: 450px) {
    .landing-over h1 {
        font-size: 60px;
    }
    .landing-over p {
        font-size: 18px;
    }
    .btn--large {
        font-size: 18px;
        padding: 10px 30px;
    }
}

@media screen and (max-width: 360px) {

    .landing-over h1 {
        font-size: 50px;
    }
    .landing-over p {
        font-size: 17px;
    }
    .btn--large {
        font-size: 17px;
        padding: 8px 25px;
    }

    

}
@media screen and (max-width: 280px) {
    .landing-over h1 {
        font-size: 40px;
    }
    .landing-over p, .btn--large  {
        font-size: 16px;
    }
   
}
.services {
    padding: 60px 0 0 0
}
.services.bg {
    background-color: #f6f6f6;
}
.services .not {
    display: none;
}
.service-head {
   
    text-align: center;
}
.service-head h1{
    margin-bottom: 20px;
    color: #1f1f25;
    font-size: 50px;
}
.service-head p {
    margin: 30px 0;
    font-size: 22px;
    line-height: 30px;
    color: rgba(29,29,36,.75);
}

.allService {
    padding: 0 70px;
    display: grid;
    grid-template-columns: auto auto auto;
    grid-column-gap: 50px;
    position: relative;
    margin: 20px 0;
}

.service_section {
    background-color: #f6f6f6;
    padding: 40px 30px;
    margin: 20px 0;
    transition: all 0.5s ease-in-out;
    border-radius: 20px;
}

.service_section.center {
    text-align: center;
}

.service_section:hover{
    background-color: #f9004d;
    transform: scale(1.03);
}

.service_section:hover .icon,
.service_section:hover .content .title,
.service_section:hover .content p {
    transition: all 0.5s ease-in-out;
    color: #fff;
}


.service_section a {
    text-decoration: none;
}


.service_section .icon {
    font-size: 45px;
    font-weight: 400;
    margin-bottom: 23px;
    color: #f9004d;
    
}
.service_section .content .title {
    color: #1f1f25;
    margin: 20px 0;
    font-size: 22px;
    font-weight: 500;
    text-transform: capitalize;
    letter-spacing: 1px;
}
.service_section .content p {
    color: rgba(29,29,36,.75);
    font-size: 20px;
    line-height: 30px;
    
}

@media screen and (max-width: 1130px) {

    .allService {
        padding: 0 20px;
        grid-column-gap: 20px;
    }
}

@media screen and (max-width: 991px) {
    .allService {
        display: grid;
        grid-template-columns: auto auto ;
        grid-column-gap: 50px;
    }
    .service-head {
        padding: 0 60px;
    }
}

@media screen and (max-width: 840px) {
    .allService {
       
        grid-column-gap: 10px;
    }
    .service_section {
        padding: 35px 20px;
    }
    .service_section .icon {
        font-size: 38px;
    }
    .service_section .content .title {
        font-size: 20px;
    }
    .service_section .content p {
        font-size: 18px;
    }
    .service-head {
        padding: 0 40px;
    }
    .service-head h1 {
        font-size: 45px;
    }
    .service-head p {
        font-size: 20px;
    }
}


@media screen and (max-width: 576px) {
    .services {
        padding: 20px 0 0 0
    }

    .allService {
        display: grid;
        grid-template-columns: auto ;
        grid-column-gap: 50px;   
    }
    .service-head {
        padding: 0 5px;
    }
    .service-head h1 {
        font-size: 37px;
    }
    
}
@media screen and (max-width: 285px) {
    .service-head h1 {
        font-size: 27px;
    }
    .service-head p {
        font-size: 18px;
    }
}
.slider-content {
    background-color: #f8f9fc;
    padding: 80px 0;
}
.slider-content .s-head {
    padding: 0 70px;
}

.slider-content .s-head h1 {
    font-size: 60px;
    margin-bottom: 8px;
    letter-spacing: 1px;
}
.slider-content .s-head p {
    font-weight: 400;
    color: #717173;
    max-width: 600px;
    line-height: 2;
    font-size: 20px;
}
.slider-content .slider {
    margin: 50px;
    padding: 0;
    box-sizing: border-box;
}
.slider-content .content {
    border: none;
    outline: none;
    display: flex;
    justify-content: center;
}
.slider-content .card-img p {
    color: #e4e4e4;
    font-size: 16px;
    text-transform: capitalize;
}

.slider-content .card-img h1 {
    color: #fff;
    font-size: 28px;
    line-height: 36px;
    margin: 25px 0;
}
.slider-content .card-img a {
    color: #fff;
    text-decoration: none;
    border:1px solid hsla(0,0%,100%,.9);
    padding: 13px 21px;
    border-radius: 4px;
    transition: all 0.5s ease-in-out;
}
.slider-content .card-img a:hover {
    background-color: #f6004c;
    border: 1px solid #f6004c; 
}

.slider-content .card-img {
    position: relative;
    max-width: 95%;
    transition: all 0.4s ease-in-out;
    
}
.slider-content .card-img img {
    
   width: 100%;
   height: 480px;
   object-fit: cover;
}

.slider-content .special-content {
    position: absolute;
    bottom: -75px;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    z-index: 999;
    color: #ffff;
    padding: 20px 40px;
    transition: all 0.5s ease-in-out;
}

.slider-content .card-img .s-content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    z-index: 999;
    color: #ffff;
    padding: 20px 40px;
}

.slider-content .card-img .over {
    
    transition: all 0.4s ease-in-out;
    position: absolute;
    z-index: 222;
    top: 0;
    left: 0;
    background-image: linear-gradient(#252525 10%,#000);
    width: 100%;
    height: 100%;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    opacity: 0.4  ;
}



.slider-content .card-img:hover .over {
    transition: all 0.4s ease-in-out;
    background-image: linear-gradient(#f6004c 10%,#000);
    opacity: 0.8;
}

.slider-content .card-img:hover {
    transform: scale(1.05);
}

.slider-content .card-img:hover .special-content {
    bottom: 0;
}

.slick-dots li.slick-active button:before {
    color: #f6004c;
    font-size: 9px;
}

.slick-dots {
    bottom: -50px;
}

@media screen and (max-width: 991px) {
    .slider-content .s-head {
        padding: 0 30px;
    }
    .slider-content .slider {
        margin: 50px 20px;
    }
}


@media screen and (max-width: 600px) {
    
    .slider-content .s-head h1 {
        font-size: 50px;
    }
    .slider-content .card-img {
        
        max-width: 100%;
    }






    .slider-content .special-content {
        
        bottom: 0
    }



 

    .slider-content .card-img .over.special-over {
    
        transition: all 0.4s ease-in-out;
        position: absolute;
        z-index: 222;
        top: 0;
        left: 0;
        background-image: linear-gradient(#f6004c 10%,#000);
        width: 100%;
        height: 100%;
        color: #fff;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-start;
        opacity: 0.8  ;
    }
    


}


@media screen and (max-width: 360px) {
    .slider-content .s-head h1 {
        font-size: 40px;
    }
    .slider-content .s-head p {
        font-size: 17px;
    }
}

@media screen and (max-width: 280px) {
    .slider-content .s-head h1 {
        font-size: 35px;
        margin-bottom: 25px;
    }
    .slider-content .s-head p {
        font-size: 16px;
    }
}
