    .contact-content .content .icon {
        height: 60px;
        width: 60px;
        line-height: 60px;
        font-size: 23px;
        top: -35px;
    }
    .contact-content .content p {
        font-size: 16px;
    }
    .contact-content .content {
        
        padding: 40px 15px;
    }
    .contact-form .form h1 {
        font-size: 40px;
    }
    .contact-form .image img {
        height: 350px;
    }
    .contact-form .form form button {
        padding: 15px 25px
    }
}
@media screen and (max-width: 320px) {
    .contact-form .image img {
        height: 300px;
    }
}
.details {
    padding: 60px 70px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.details .image {
    margin-bottom: 30px;
}
.details .image img {
    max-width: 100%;
}
.details .info {
    padding: 0 20px;
    margin: auto 0;

}
.details .info .list {
    list-style: none;
}
.details .info p {
    line-height: 1.8;
    text-transform: capitalize;
    margin: 0 0 20px 0;
    font-size: 18px;
    color: rgba(29,29,36,.75);
}
.details .info h3 {
    font-size: 36px;
    line-height: 30px;
    font-weight: 500;
    margin-top: 40px;
    margin-bottom: 27px;
}
.details .info .list .list-item::before {
    
    position: absolute;
    content: "";
    width: 6px;
    height: 6px;
    border-radius: 100%;
    background: rgba(29,29,36,.75);
    left: 0;
    top: 10px;
}
.details .info .list .list-item {
    font-size: 18px;
    line-height: 30px;
    color: rgba(29,29,36,.75);
    position: relative;
    padding-left: 20px;
}
@media screen and (max-width: 775px) {
    .details {
        padding: 60px 30px;
    }
}
@media screen and (max-width: 575px) {
    .details {
        padding: 60px 15px;
    }
    .details .info p {
        font-size: 16px;
    }
    .details .info .list .list-item {
        font-size: 16px;
    }
    .details .info h3 {
        font-size: 27px;
    }
}
.goTop {
    position: fixed;
    bottom: 30px;
    right: 20px;
    background-color: #f9004d;
    color: #fff;
    z-index: 1000;
    border-radius: 50%;
    border: 1px solid #f9004d;
    cursor: pointer;
    padding: 10px;
    transition: all 0.5s ease-in-out;
}
.goTop:hover {
    transition: all 0.5s ease-in-out;
    background-color: #fff;
    color: #f9004d;
}
.footer {
    background-color: #1d1d24;
    display: grid;
    grid-template-columns: auto auto auto;
    align-items: center;
    padding: 40px 40px;
}
.footer .logo {
    width: 110px;
}
.footer .logo img {
    max-width: 100%
}
.footer .social {
    list-style: none;
    display: flex;
    justify-content: center;
}

.footer .social .list-item:hover a {
    transition: all 0.4s ease-in-out;
    background-color: #f9004d;
    border:2px solid #f9004d;
    color: #fff;
}
.footer .social .list-item:hover {
    transition: all 0.4s ease-in-out;
    transform: translateY(-5px);
}

.footer .social .list-item a {
    text-decoration: none;
    padding: 10px;
    margin: 0 15px;
    border:2px solid rgba(198,201,216,.75);
    border-radius: 50%;
    font-size: 16px;
    color: rgba(198,201,216,.75);
    transition: all 0.4s ease-in-out;
}
.footer .copy p {
    color: #c6c9d8;
    font-size: 16px;
    opacity: .75;
    text-align: center;
}

@media screen and (max-width: 900px) {
    .footer {
        grid-template-columns: auto auto 
    }
    .footer .copy {
        width: 150%;
        text-align: center;
        margin: 20px auto 10px;
        justify-content: center;
    }
}
@media screen and (max-width: 775px) {
    .footer .copy {
        width: 170%;
    }
    .footer .copy p {
        font-size: 14px;
    }
}
@media screen and (max-width: 575px) {
    .footer {
        grid-template-columns: auto;
        justify-content: center; 
    }
    .footer .logo {
        text-align: center;
        width: 100%;
    }
    .footer .logo img {
        max-width: 150px;
    }

    .footer .social {
        margin: 20px 0;
        justify-content: space-around;
    }
    .footer .copy {
        width: 100%;
        margin: 20px auto 10px;
        
    }
}
@media screen and (max-width: 400px) {
    .footer {
        padding: 20px;
    }
    .footer .logo img {
        max-width: 100px;
    }
    .footer .social .list-item a {        
        padding: 8px;
        margin: 0 10px;
        font-size: 14px;
    }
}
